const PUSH_TO_CART = 'PUSH_TO_CART';

const initialState = {
    cart: [
        { id: 7, pic: 'https://images.ua.prom.st/1160969677_w640_h640_1160969677.jpg', title: 'Alize Alpaca Royal 567', description: '567 зеленый меланж Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 8, pic: 'https://images.ua.prom.st/1160955652_w640_h640_1160955652.jpg', title: 'Alize Alpaca Royal 55', description: '55 белый Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 2, pic: 'https://images.ua.prom.st/1160968353_w640_h640_1160968353.jpg', title: 'Alize Alpaca Royal 262', description: '262 светлый беж Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 3, pic: 'https://images.ua.prom.st/1160969329_w640_h640_1160969329.jpg', title: 'Alize Alpaca Royal 466', description: '466 карамель Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 4, pic: 'https://images.ua.prom.st/1379180027_w640_h640_1379180027.jpg', title: 'Alize Alpaca Royal 02', description: '02 шафран Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' }       
    ]
};

const cartReducer = (state = initialState, action) => {
    switch (action.type) {
        case PUSH_TO_CART:
            let item = action.payload;
            return {
                ...state,
                cart: [...state.cart, {id: item.id, pic: item.pic, title: item.title}]
            }
        default:
            return state;
    };
};

export const pushToCart = (pushedItem) => ({type: PUSH_TO_CART, payload: pushedItem });

export default cartReducer;
