import React from 'react';
import { connect } from 'react-redux';
import ItemInCart from './../Common/ItemInCart/ItemInCart';

const Cart = (props) => {
    const cartItems = props.cart;
    const renderCart = cartItems.map(i => {
        return <ItemInCart pic={i.pic} title={i.title} />
    })

    return (
        <div>
            cart1
            <div>
                {renderCart}
            </div>
        </div>
    )
};

let mapStateToProps = (state) => {
    return {
        cart: state.cartReducer.cart,
    }
}

export default connect(mapStateToProps)(Cart);